import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useLocation } from 'react-router-dom';

const Nav = ({ menuIsActive, setMenuIsActive }) => {
  const { pathname } = useLocation();
  return (
    <StyledNav menuIsActive={menuIsActive}>
      <h1>
        <StyledLink onClick={() => setMenuIsActive(!menuIsActive)} to='/'>
          Video Production
        </StyledLink>
      </h1>
      <ul>
        <li>
          <StyledLink onClick={() => setMenuIsActive(!menuIsActive)} to='/'>
            About Us
          </StyledLink>
          <Line
            initial={{ width: '0' }}
            animate={{ width: pathname === '/' ? '100%' : '0' }}
            transition={{ duration: 0.7 }}
          />
        </li>
        <li>
          <StyledLink onClick={() => setMenuIsActive(!menuIsActive)} to='work'>
            Our Work
          </StyledLink>
          <Line
            initial={{ width: '0' }}
            animate={{ width: pathname === '/work' ? '100%' : '0' }}
            transition={{ duration: 0.7 }}
          />
        </li>
        <li>
          <StyledLink
            onClick={() => setMenuIsActive(!menuIsActive)}
            to='contact'
          >
            Contact Us
          </StyledLink>
          <Line
            initial={{ width: '0' }}
            animate={{ width: pathname === '/contact' ? '100%' : '0' }}
            transition={{ duration: 0.7 }}
          />
        </li>
      </ul>
    </StyledNav>
  );
};

const StyledLink = styled(Link)`
  font-size: 1.5rem;
  justify-self: space-around;
`;

const StyledNav = styled.nav.attrs()`
  z-index: 10;
  position: sticky;
  top: 0;
  background-color: #282828;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 10vh;
  margin: auto;
  padding: 1rem 10rem;
  a {
    text-decoration: none;
    color: white;
    transition: all 0.3s;
    &:hover {
      color: grey;
    }
  }
  ul {
    list-style: none;
    display: flex;
  }
  li {
    margin-left: 10rem;
    cursor: pointer;
    transition: all 0.5s;
    position: relative;
  }
  @media (max-width: 1300px) {
    flex-direction: column;
    padding: 2rem 0;
    ul {
      padding: 4rem 0 2rem;
      justify-content: space-around;
      width: 100%;
    }
    li {
      margin-left: 0;
    }
  }
  @media (max-width: 760px) {
    min-height: 100vh;
    display: ${(props) => (props.menuIsActive ? 'block' : 'none')};
    h1 {
      text-align: center;
      font-size: 1.5rem;
      font-weight: 700;
    }
    ul {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      gap: 6rem;
      margin-top: 30%;
    }
  }
`;
const Line = styled(motion.div)`
  height: 0.3rem;
  background: #23d997;
  width: 0;
  position: absolute;
  left: 0;
`;

export default Nav;
