import React from 'react';
import styled from 'styled-components';

const NavIconOpener = ({ setMenuIsActive, menuIsActive }) => {
  
  return (
    <StyledNavIcon>
      <SVG onClick={() => setMenuIsActive(!menuIsActive)}
        width='70'
        height='70'
        viewBox='0 0 100 100'
        fill='none'
        xmlns='http://www.w3.org/2000/svg'
      >
        <rect
          className='top'
          x='20'
          y='29'
          width='60'
          height='7.85106'
          fill='#fff'
        />
        <rect
          className='middle'
          x='20'
          y='45.5745'
          width='60'
          height='7.85106'
          fill='#fff'
        />
        <rect
          className='bottom'
          x='20'
          y='62.1489'
          width='60'
          height='7.85106'
          fill='#fff'
        />
      </SVG>
    </StyledNavIcon>
  );
};

const StyledNavIcon = styled.div`
  display: none;
  @media (max-width:760px) {
    display: block;
  }
  z-index: 200;
  position: fixed;
  top: 20px;
  right: 20px;
`;
const SVG = styled.svg`
  cursor: pointer;
  rect {
    transition: all 0.3s ease-in-out;
  }
`;

export default NavIconOpener;
